.btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  bottom: 2px;
  right: 5px;
}

.btn-watched, .btn-queue {
  min-width: 130px;
  height: 40px;
  color: #fff;
  cursor: pointer;
  z-index: 1;
  background: none;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 5px 10px;
  font-weight: bold;
  transition: all .3s;
  display: inline-block;
  position: relative;
  box-shadow: inset 2px 2px 2px rgba(216, 100, 5, .804), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.btn-watched:hover:after, .btn-queue:hover:after {
  cursor: pointer;
  width: 100%;
  left: 0;
}

.active {
  background-color: #f47d0e;
}

.btn-watched:after, .btn-queue:after {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: #f47d0e;
  border-radius: 5px;
  transition: all .3s;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.btn-queue:active, .btn-watched:active {
  top: 2px;
}

.btn-container {
  justify-content: center;
  gap: 19.94px;
  padding-top: 49px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .btn-container {
    gap: 15px;
